import '../App.css';
import MenuBar from './MenuBar'
import {useState, useEffect} from 'react' 
import {HashLink as HLink} from 'react-router-hash-link'
// days since i have started coding: (today date - 09/12/2017) days`
// graduadted: t`oday date - 31/12/2020

// Who I am

// Personal values

// Interests and Passion - Teaching - Youtube
// Artificial intelligence
// Badminton
// games
// books and mangas - Stormlight Archive & Doraemon - talk about Syl and  Doraemon -> AI 

const Home = () => {
    const [currentDatetime, setCurrentDatetime] = useState(new Date())
    const graduationDate = new Date(2020, 11, 1)
    const firstDayCoding = new Date(2017, 10, 21)
    const MILLISECS_IN_A_DAY = 1000 * 60 * 60 * 24
    useEffect(() => {
      setInterval(() => setCurrentDatetime(new Date()), 1000)
    })

    const daysBetween = (latestDate, olderDate) => {
      return Math.round((latestDate - olderDate) / (MILLISECS_IN_A_DAY))
    } 

    return (
      <div className="Home">
        <MenuBar active={0}></MenuBar>
        <h1 className="page-name">Home</h1>
        <div className="white-page">
          <div className="home-title">
            <h1>Zhi Hao Tan</h1>
            <h2>About</h2>
          </div>
          <div className="home-about-section">
            <p>I am a computer science graduate from Monash University, majoring in software development with high 
              distinction average across all units. I am passionate about many of the different fields in computer 
              science e.g. algorithms and data structures, artificial intelligence, cybersecurity, software development, 
              design patterns, programming paradigms, usability and UX, parallel computing, distributed computing, and optimization.
              <br/><br/>I am currently into web development, accessibility, and responsive design.
              Much of what I am enjoy in the field of computer science stems from my love for patterns, maths, problem-solving,
              and philosophy. For example, artificial intelligence ties closely with the philosophy of consciousness and cognition.
              <br/><br/>Maybe due to the fact that I am personally acquainted with a few doctors and a doctor to-be, I have 
              some <HLink to="/PersonalWebsite/#home-interests" className="links">interests</HLink> in
              health IT. After 
              meeting <a className="links" href="https://research.monash.edu/en/persons/arun-konagurthu" target="_blank">Arun Konagurthu</a> and 
              Taylor Kearney, two amazing teachers, I gained interest in teaching/tutoring. I applied and got accepted as a tutor
              for the unit Advanced Algorithms and Data Structures in Monash and will be tutoring starting from March 2021 - July 2021.
            </p>
          </div>
        </div>
        <br/>
        <br/>
        {/* Flex this */}
        <div className="home-statistics">
          <div className="home-stats-item">
            It is currently: {currentDatetime.toLocaleTimeString('en-US', {timeZoneName: 'short'})}
          </div>
          <div className="home-stats-item">
            Days since graduation: {daysBetween(currentDatetime, graduationDate)}
          </div>
          <div className="home-stats-item">
            Days since I started coding: {daysBetween(currentDatetime, firstDayCoding)}
          </div>
        </div>
        <br/>
        <div className="white-page">
          <div className="home-personal-values-section" id="#home-personal-values">
            <div className="home-title">
              <h2>Personal Values</h2>
            </div>
            <div className="home-section-entry">
              <h3>Love</h3>
              <p>
                Love others as yourself and do unto them what you want done unto you. Help others in their times of needs.
              </p>
              <h3>Respect</h3>
              <p>
                Respect everyone as fellow humans and respect our differences.
              </p>
              <h3>Accountability</h3>
              <p>
                Keep promises and deliver what was agreed upon.
              </p>
              <h3>Integrity</h3>
              <p>
                Stay honest, upright and just.
              </p>
              <h3>Perseverance</h3>
              <p>Never give up and give a 100% in everything I do.</p>
            </div>
          </div>
        </div>
        <br/>
        <br/>
        <div className="white-page">
          <div className="home-interests-section" id="home-interests">
            <div className="home-title">
              <h2>Interests</h2>
            </div>
            <div className="home-section-entry">
              <h3>Optimization</h3>
              <p>
                Let's go faster and be more efficient!
              </p>
              <h3>Patterns</h3>
              <p>
                I love patterns! Fibonacci sequence, golden ratio, square number series, and their relationships.
              </p>
              <h3>Maths</h3>
              <p>
                Because there are lots and lots of patterns.
              </p>
              <h3>Data Structures</h3>
              <p>
                Data Strucutres facilitates efficiency and are awesome. What's better than data structures and patterns? Patterns in data structures: Fibonacci Heap!
              </p>
              <h3>Algorithms</h3>
              <p>
                Good algorithms are beautiful. They are as picturesque as the starry nights with shooting stars on the beach. Ukkonen's algorithm is breathtaking.
              </p>
              <h3>Cybersecurity</h3>
              <p>
                If algorithms are beautiful, cybersecurity is cool. Nothing more needs to be said.
              </p>
              <h3>Health IT</h3>
              <p>
                I participated in a research project: Building Machine Learning Capable Mobile Apps to predict if a patient will
                have complicatins when taking a medication. Would love to work on more projects related to Health IT.
              </p>
              <h3>Badminton</h3>
              <p>
                My favorite sports :)
              </p>
              <h3>Books, Mangas and Light Novels</h3>
              <p>
                Books: Stormlight Archive is my favorite series. My favorite character is Syl. She is a spren, a floating creature who is the main character's best friend.
                <br clear=""></br>
                <img className="book-image" src="data:image/jpeg;base64," alt="Stormlight Archive cover" width="200" height="230"/>
                <br/>
                Mangas: Doraemon is one of my favorites. Doraemon is a robot cat from the future and is intelligent. He is adorable.
                <br clear=""></br>
                <img className="book-image" alt="Doraemon image" src="data:image/jpeg;base64,"></img>
                <br/>
                Light novels: I love 'The time I got reincarnated as a slime'. Basically, the main character has an advanced AI attached to his head.
                <br clear=""></br>
                <img className="book-image" alt="Rimuru image" src="data:image/jpeg;base64,"></img>
              </p>
              <h3>Artificial Intelligence</h3>
              <p>
                From my favorite books, it can be seen that I think having a companion or an AI friend is amazing.  
              </p>
              <h3>Philosophy</h3>
              <p>
                I am interested in the philosophy of consciousness and cognition, perhaps for the same reasons I am interested in artificial intelligence.
                Philosophy also serves as a flavour enhancer for the dish that is artificial intelligence.
              </p>
              <h3>Teaching</h3>
              <p>
                I want to give what I have received. I will be working as a tutor in Monash and I have also startd a Youtube channel to teach what I have learned: <a href="https://www.youtube.com/channel/UCcGa7qhbDJcHMW3eaXyJeiQ">Aspiring Computer Scientist</a>
              </p>
            </div>
          </div>
        </div>
      </div>
    )
  }

  export default Home;

